import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IonicPage, NavController, NavParams, ViewController, AlertController, LoadingController } from 'ionic-angular';
import { SettingsService } from '../../providers/settings-service';
import { User, UserService } from '../../providers/user-service';

/**
 * Generated class for the AccountUpdatePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
	selector: 'page-account-update',
	templateUrl: 'account-update.html',
	providers: [SettingsService, UserService]
})
export class AccountUpdatePage {
	user: User;
	submitted = false;
	postalCodeValidation = {
		valid: true,
		spinner: false,
		message: ""
	}
	loading: any;

	constructor(public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public UserService: UserService, public settingsService: SettingsService,  public loadingCtrl: LoadingController) {
		this.user = this.navParams.data.user;
		// console.log(this.user);
	}

	ionViewDidLoad() {
		// console.log('ionViewDidLoad AccountUpdatePage');
	}

	dismiss(data?: any) {
		// using the injected ViewController this page
		// can "dismiss" itself and pass back data
		this.viewCtrl.dismiss(data);
	}

	onUpdate(form: NgForm) {
		this.submitted = true;

		if (form.valid && this.postalCodeValidation.valid) {
			this.loading = this.loadingCtrl.create();
			this.loading.present().then(()=>{
				this.UserService.update(this.user).then(data => {
					this.loading.dismiss();
					this.viewCtrl.dismiss(this.user);
					this.alert("Sucesso", "Dados alterados com sucesso.");
				}, error => {
					this.loading.dismiss();
					this.alert("Erro", "Ocorreu um erro.");
				});
			})
		}
	}

	checkLocation(postal_code: string) {
		if(postal_code.length == 8) {
			this.postalCodeValidation.spinner = true;
			this.settingsService.getDistance(postal_code).then(data => {
				if(data.status == "success") {
					if(data.valid) {
						this.postalCodeValidation.valid = true;
						this.postalCodeValidation.message = data.address;
						this.user.locality = data.locality;
					}
					else {
						this.postalCodeValidation.valid = false;
						this.postalCodeValidation.message = data.address  + " Não se encontra dentro do nosso raio de entrega. Consulte o nosso mapa para mais informações.";
					}
				}
				else {
					this.postalCodeValidation.valid = false;
					this.postalCodeValidation.message = "Código postal inválido.";
				}
				this.postalCodeValidation.spinner = false;
			}, error => {
				this.postalCodeValidation.valid = false;
				this.postalCodeValidation.spinner = false;
				this.postalCodeValidation.message = "Ocorreu um erro a calcular.";
			});
		}
		else {
			this.user.locality = '';
		}
	}

	alert(title: string, subtitle?: string) {
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: subtitle,
			buttons: ['Ok']
		});
		alert.present();
	}

}



